<template>
  <div>
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <div class="btn-block">
        <div @click="showDialog()" class="btn-main mr15">导入工资表</div>
        <div @click="$router.push('/personnel/salary/template')" class="btn-main-deepen">设置工资模板</div>
      </div>
      <search-box :searchData="searchData" :table="table"></search-box>
      <resize-table>
      <table class="table">
        <thead>
        <tr>
          <th width="8%">姓名</th>
          <th width="8%">年级</th>
          <th width="14%">科目</th>
          <th width="12%">基本工资(元)</th>
          <th width="12%">应发数(元)</th>
          <th width="12%">实发数(元)</th>
          <th width="10%">操作</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="(item,key) in table.list">
          <td>{{item.name}}</td>
          <td>{{item.gradeName}}</td>
          <td>{{item.courseName}}</td>
          <td>{{item.salaryBase}}</td>
          <td>{{item.salaryShould}}</td>
          <td>{{item.salaryReality}}</td>
          <td>
            <span @click="jumpDetail(item)" class="oper">详情</span>
          </td>
        </tr></tbody>
      </table>
      </resize-table>
      <no-data v-show="!table.noData"></no-data>
      <paging :data.sync="table"></paging>
      <el-dialog title="导入工资表" class="dialog" :visible.sync="dialog" width="540px">
        <div class="item">
          <span class="title imp">工资月份</span>
          <el-date-picker
              class="input"
              format="yyyy年MM月"
              :clearable="false"
              v-model="salaryMonth"
              type="month"
              placeholder="选择时间">
          </el-date-picker>
        </div>
        <div class="item">
          <span class="title imp">上传文件</span>
          <el-input type="file" class="input file-input" ref="upload"
                    accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
                    placeholder="请选择"></el-input>
          <a href="javascript:void(0)" class="f-small main-c ml5" @click="downloadTpl">下载模板</a>
        </div>
        <div class="btns">
          <div @click="impFun()" class="btn-submit btn-upload mr15">确认</div>
          <div @click="dialog = false" class="btn-clear">返回</div>
        </div>
      </el-dialog>
    </main-part>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        nav: [{name: "工资信息", path: ""}],
        searchData: [
          {type: 'date', title: '时间', dateType: 'month',
             model: 'salaryMonth', initialDate: new Date()},
        ],
        table:{
          url: '/salary/querySalaryPage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            salaryMonth: new Date().$Format('yyyy-MM-dd hh:mm:ss'),
            keywords: null
          }
        },
        dialog: false,
        // 导入中的月份
        salaryMonth: new Date()
      }
    },
    methods: {
      // 展现导入工资表弹窗
      showDialog(){
        this.salaryMonth = null
        this.dialog = true
      },
      jumpDetail(item){
        this.$router.push({
          path:`/personnel/salary/${item.id}`,
          query: {
            date: new Date(this.table.query.salaryMonth).$Format('yyyy年MM月')
          }
        })
      },
      // 导入工资表
      async impFun() {
        if(!this.salaryMonth){
          this.$message.warning('请选择月份')
          return
        }
        let file = this.$refs.upload.$refs.input.files[0]
        if(!file){
          this.$message.warning('请选择上传文件')
          return
        }
        let formData = new FormData()
        formData.append('file', file)
        formData.append('salaryMonth', new Date(this.salaryMonth).$Format('yyyy-MM-dd hh:mm:ss'),)
        let obj = {
          url: '/salary/importSalaryList.do',
          query: formData,
          el: '.btn-upload',
          noText: true
        }
        // 上传模板拿到返回的模板地址
        let res = await this.$ajax(obj)
        if(res.errMsgList && res.errMsgList.length){
          this.$message.error(res.errMsgList[0])
        }
        this.dialog = false
        this.$remind('导入成功')
        this.$search(this.table)
      },
      // 下载模板
      downloadTpl(){
        if(!this.salaryMonth){
          this.$message.warning('请选择工资月份')
          return
        }
        let url = '/export/salaryTemplate.do'
        let month = this.salaryMonth.$Format('yyyy-MM-dd hh:mm:ss')
        window.open(this.$conf.API_PATH + url  + `?salaryMonth=${month}`)
      }
    },
    async mounted(){
      this.$search(this.table)
    }
  }
</script>
<style lang="sass" scoped>
  .dialog
    .title
      width: 80px
      line-height: 40px
      vertical-align: top
      display: inline-block
    .input
      width: calc(100% - 140px)
    .imp:before
      content: '*'
      color: $error-c
      margin-right: 4px
    .file-input
      /deep/ input
        padding-top: 10px
        line-height: 1
    .item
      margin-bottom: 20px
    .btns
      text-align: center

</style>
